.sidenav {
  @include flex(column, flex-start, flex-start);
  @include boxshadow;
  padding: 20px 0;
  background-color: #fff;
  z-index: 999;
  transform: translateY(-530px);
  transition: 0.25s;
  opacity: 0;
  &__list {
    @include flex(column, flex-start, flex-start);
    height: 400px;
  }
}
.sidenav--isOpen {
  transform: translateY(0);
  opacity: 1;
}
.sidenav--isDark {
  background-color: $main;
}
